<template>
	<div>
		<div class="search_wrap">
		   <el-form  :inline="true" class="demo-form-inline">
			  	<el-form-item label="省区经理：">
			    	<el-input v-model="provinceManager" placeholder="请输入省区经理名称"></el-input>
			  	</el-form-item>
			  	<div class="div-inline el-form-item">
			 		  <depart-componet departMsg="事业部：" @selectSuccess = "selectSuccess"></depart-componet>
			 	  </div>
			  	<el-form-item label="所属省区：">
			    	<el-input v-model="provinceName" placeholder="请输入省区"></el-input>
			  	</el-form-item>
         <el-form-item label="回款方式：">
           <el-select v-model="collectionWay" placeholder="请选择回款方式">
             <el-option label="全部" value='-1'>全部</el-option>
             <el-option v-for="(item,key,index) in collectionWayList" :label="item" :value="key" :key="index"></el-option>
           </el-select>
         </el-form-item>
			  	<div class="div-inline el-form-item">
			  		<div class="div-inline">回款时间：</div>
				  	<datepicker startIsShow = "true" endIsShow= "true" class="div-inline" @changeSelect="changeSelect"></datepicker>
			  	</div>
			  <div class="div-inline el-button-red" @click = "toInquire()">查询</div>
		  	  <div class="div-inline el-button-red" @click = 'exportBaseInfoData'>导出</div>
		  	</el-form>
		</div>
		<div class="table_wrap">
			<el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark">
			    <el-table-column prop="createDate" label="回款时间"></el-table-column>
			    <el-table-column prop="departmentId" label="事业部"  width="150">
			    	<template slot-scope="scope">
				        {{scope.row.departmentId == 1 ? "心脑事业部" : scope.row.departmentId == 2 ? "风湿及骨科事业部" : scope.row.departmentId == 3 ? "云事业部" : "消化事业部"}}
				    </template>
			    </el-table-column>
			    <el-table-column prop="provinceName" label="所属省区"></el-table-column>
			  	<el-table-column prop="provinceManager" label="省区经理"></el-table-column>
			    <el-table-column prop="mobile" label="联系电话"></el-table-column>
			    <el-table-column prop="mobile" label="回款方式">
            <template slot-scope="scope">
              <span>{{scope.row.collectionWay==1?'商业公司回款':scope.row.collectionWay==2?'个人回款':''}}</span>
            </template>
          </el-table-column>
			    <el-table-column prop="billName" label="回款商业"></el-table-column>
			    <el-table-column prop="actualCollectionPrice" label="回款金额"></el-table-column>
			    <el-table-column prop="collectionType" label="回款类型">
			    	<template slot-scope="scope">
				        {{scope.row.collectionType == 1 ? "货款" : ""}}
				    </template>
			    </el-table-column>
			    <el-table-column label="回款证明">
			    	<template slot-scope="scope">
				        <div class="returnProve" v-for="(item, index) in scope.row.imgsList" :key="index">
				        	<img :src="item" alt="" />
				        </div>
				    </template>

			    </el-table-column>
			    <el-table-column prop="status" label="状态">
			    	<template slot-scope="scope">
				        {{scope.row.status == 0 ? "待审核" : scope.row.status == 1 ? "审核通过" : "已驳回"}}
				    </template>
			    </el-table-column>
			    <el-table-column prop="address" label="操作" width="150">
			    	<template slot-scope="scope">
              <div v-if="scope.row.status == 0 " class="operateBtn" @click="toCheck(scope.row.id)">审核</div>
              <div v-if="scope.row.status == 1 ">已审核</div>
              <div v-if="scope.row.status == 2 " class="operateBtn" @click="reasonRefuse(scope.row.id)">驳回原因</div>
              <div class="operateBtn" @click="view(scope.row.id)">查看</div>
				    </template>
			    </el-table-column>
		  	</el-table>
	  		<el-pagination :pageIndex='pageIndex' :pageSize='pageSize' :totalRecords='totalRecords' :totalPageCnt='totalPageCnt' :pagin_show='pagin_show' v-on:clickpage="updataPage"></el-pagination>
	 	</div>
	 	<promptDialog ref="reaRefuseDialog" :promptMsg="promptMsg"></promptDialog>

	 	<el-dialog title="提示" :visible.sync="dialogCheckVisible" :append-to-body='true' width="35%">
			<div class= 'dialog_body_content'>
				<div class="checkResult">
					审核结果：
					<el-radio-group v-model="radioActive" class="selectRadio">
						<el-radio :label="1">通过</el-radio>
		    			<el-radio :label="2">不通过</el-radio>
				    </el-radio-group>
				</div>
				<div class="checkFail" v-if="radioActive==2">
					审核原因：
					<textarea class="checkReason" v-model="checkReason" name="" id="" cols="30" rows="10"></textarea>
				</div>
			</div>
			<div slot="footer" class="dialog-footer">
				<span class="dialog-cancel-btn dialoperateBtn" @click="dialogPWDVisible = false">取消</span>
    			<el-button v-show="radioActive=='' || radioActive==2 && checkReason==''" type="danger" class="dialogDisaBtn" disabled>确定</el-button>
				<span v-show="radioActive==1 || radioActive==2 && checkReason!=''" class="dialog-sure-btn dialoperateBtn" @click="checkPass()">确定</span>
			</div>

		</el-dialog>

	</div>

</template>

<script>
	import departComponet from 'src/headComponent/departComponent'
	import datepicker from 'components/data-plugin'
	import addressPlugin from 'components/address';
	import elPagination from 'components/el-page';
	import promptDialog from 'components/promptDialog'
  	import {getRetuMoneyData, exportRetuMoneyData, saveCheckData, reasonRefuseData } from 'src/api/headquarter_url/financialManage';

  export default {
    data() {
      return {
      	startDate: '',//开始日期
        endDate: '',//结束日期
      	provinceManager: '',//省区经理名称
      	provinceName: '',//省区名称
      	departmentId: '',//事业部
        collectionWay:'',//回款方式
        collectionWayList: {1:'商业公司回款', 2:'个人回款'},//回款方式下拉列表

		tableData:[],//table列表
      	totalRecords: 0,//总条数
      	totalPageCnt: 0,//总页数
      	pageIndex: 1,//当前页
      	pageSize:10,
      	pagin_show:true,

      	promptMsg:'', //提示信息
      	rejectReason:'', //驳回原因

      	dialogCheckVisible: false,//审核
      	radioActive:'',//审核结果
      	checkReason:'',//审核原因
      	goodsId:''//商品id

      }
    },
 	created(){
    	let datalist=this.getsearchInfo(this.pageIndex,this.pageSize)
		  this.initData(datalist)
	},
	components:{
		departComponet,
        elPagination,
        addressPlugin,
        datepicker,
        promptDialog
    },
    methods: {
    	//页面初始化
	    initData(datalist){
	    	console.log(datalist);
			//获取table数据列表和分页信息
	        getRetuMoneyData(datalist).then(res => {
	        	const datas=res.data.data;
	        	if(datas.dataList.length>0){
	        		this.totalRecords=datas.totalRecords;
		        	this.totalPageCnt=datas.totalPageCnt;
		        	this.pageIndex=datas.pageIndex;
		        	this.tableData= datas.dataList;
              this.pagin_show= true;
	        	}else{
	        		this.tableData= [];
	        		this.pagin_show= false;
	        	}

    		})
    	},
    	//获取table列表数据请求参数
		getsearchInfo(pageIndex,pageSize){
			let datalist = {
				pageIndex:pageIndex
				,pageSize:pageSize
				,startDate:this.startDate
				,endDate:this.endDate
				,provinceManager: this.provinceManager
				,collectionWay: this.collectionWay=='-1'?'':this.collectionWay
        ,provinceName: this.provinceName
        ,departmentId: this.departmentId
			};
			return datalist;
		},

		//日期组件
			changeSelect(val){
				val.type == "startDate"?this.startDate = val.data : this.endDate = val.data;
			},
			//点击查询按钮
			toInquire(){
				let datalist=this.getsearchInfo(1,this.pageSize);
				this.initData(datalist);
			},

			//点击导出基本信息按钮
			exportBaseInfoData(){
				let datalist = {
          startDate:this.startDate
          ,endDate:this.endDate
          ,provinceManager: this.provinceManager
          ,collectionWay: this.collectionWay=='-1'?'':this.collectionWay
          ,provinceName: this.provinceName
          ,departmentId: this.departmentId
				};
				exportRetuMoneyData(datalist).then(res => {
					let fileName = new Date().getTime()+'.xls';
				 	const blob = new Blob([res.data]);
				    const elink = document.createElement('a');
				    elink.download = fileName;
				    elink.style.display = 'none';
				    elink.href = URL.createObjectURL(blob);
				    document.body.appendChild(elink);
				    elink.click();
				    URL.revokeObjectURL(elink.href); // 释放URL 对象
				    document.body.removeChild(elink);
				})
			},
			//去审核
			toCheck($id){console.log($id);
				this.dialogCheckVisible = true;
				this.goodsId= $id;

			},
			//审核通过
			checkPass(){
				let datalist = {
					flag: this.radioActive,//1通过 2驳回
					ordersCollectionId: this.goodsId,
					rejectReason:this.checkReason
				};
				saveCheckData(datalist).then( res => {
					this.$message.success('审核成功');
					this.dialogCheckVisible = false;
					let datalist=this.getsearchInfo(this.pageIndex,this.pageSize);
					this.initData(datalist)
				})
			},
			//驳回原因
			reasonRefuse($id){
				this.$refs.reaRefuseDialog.dialogShow();
				reasonRefuseData({ordersCollectionId: $id}).then(res => {
					this.promptMsg= res.data.data.rejectReason;
				})
			},

			//点击事业部
			selectSuccess(val){
				this.departmentId = val;
				console.log(this.departmentId);
			},
			//点击分页
			updataPage(val){
        this.pageIndex=val.pageIndex;
        this.pageSize=val.pageSize;
				let datalist=this.getsearchInfo(this.pageIndex,this.pageSize);
				this.initData(datalist)
			},
      //点击查看
      view($id){
        this.$router.push({path:'/provinceRetuenMoneyDetailZB',query:{id:$id}});
      },

    }
  }
</script>
<style lang="scss">
	.search_wrap .province_city_county .el-form-item__content{
		width:auto;
	}
	.el-input__inner{
		padding:0 10px;
	}
</style>
<style lang='scss' scoped="scoped">

.bitchRemove{
	margin-bottom:20px;
}
.slideDown{
	display: none;
}
.spreadSlowlly{
	display: block;
}
.select-more{
	margin:0 30px;
	i{
		color:#9c9c9c;
	}
}
.el-button-red{
	margin-bottom: 20px;
}
.province_city_county{
	display: inline-block;
}
.dialog_body_content{
	padding:20px 30px;
}
.checkFail{
	margin-top:30px;
	.checkReason{
		border:1px solid #e6e6e6;
		border-radius:5px;
		width:80%;
		height:70px;
		vertical-align: middle;
		padding:10px;
		line-height:26px;
	}
}
.returnProve{
	display: inline-block;
	width:30%;
	height:30%;
	img{
		width:100%;
		height:100%;
	}

}

</style>



